import { useRecentPostByTopic } from 'utils/services';

const ProfileTopics = (props: any) => {
  const recentPostByTopic = useRecentPostByTopic({
    variables: {
      username: props.username,
    },
  });
  const topics = recentPostByTopic.data?.userByUsername?.topicsByUserId.nodes;

  let months = [
    'Jan',
    'Feb',
    'Mar',
    'Apr',
    'May',
    'June',
    'July',
    'Aug',
    'Sep',
    'Oct',
    'Nov',
    'Dec',
  ];

  return (
    <>
      {topics &&
        topics.length > 0 &&
        topics?.map((topic: any) => {
          const atLeastOnePost =
            topic.postsByTopicId.nodes.length > 0 ? true : false;
          let images = [];
          if (atLeastOnePost) {
            let content: any = [];
            topic.postsByTopicId.nodes[0].content.forEach((c: any) => {
              content.push(JSON.parse(c));
            });

            for (let current = 0; current < content.length - 1; current++) {
              if (images.length >= 2) {
                break;
              } else if (content[current].type === 'image') {
                if (content[current].content.length === 1)
                  images.push({
                    name: content[current].content[0].name,
                    url: content[current].content[0].url,
                  });
                else {
                  for (
                    let c = 0;
                    c < content[current].content.length - 1;
                    c++
                  ) {
                    if (images.length >= 2) {
                      break;
                    } else {
                      images.push({
                        name: content[current].content[c].name,
                        url: content[current].content[c].url,
                      });
                    }
                  }
                }
              }
            }
          }

          return (
            <div
              key={topic.title}
              style={{
                display: 'flex',
                flexDirection: 'column',
                alignSelf: 'flex-end',
                width: '90%',
                marginTop: '25px',
                backgroundColor: '#373737',
                color: 'white',
                borderRadius: '7px',
                padding: '10px',
              }}
            >
              {atLeastOnePost && (
                <a
                  href={`/p/${topic.postsByTopicId.nodes[0].postId}`}
                  style={{
                    maxHeight: '50px',
                    overflow: 'hidden',
                    marginBottom: '10px',
                    fontWeight: 700,
                    textDecoration: 'none',
                    color: '#FFFFFF',
                  }}
                >
                  {topic.postsByTopicId.nodes[0].description}
                </a>
              )}
              {atLeastOnePost && (
                <div>
                  {images.map((img) => {
                    return (
                      <img
                        key={img.url}
                        style={{
                          maxWidth: '75px',
                          marginRight: '20px',
                        }}
                        src={img.url}
                        alt={img.name}
                      ></img>
                    );
                  })}
                </div>
              )}
              <a
                href={`${window.location.pathname}/${topic.title}`}
                style={{
                  paddingLeft: '5px',
                  paddingTop: '2px',
                  paddingBottom: '2px',
                  marginTop: '10px',
                  fontWeight: 700,
                  fontFamily: 'Open Sans',
                  width: '100%',
                  backgroundColor: topic.topicColor,
                  borderRadius: '6px',
                  color: '#FFFFFF',
                }}
              >
                {topic.title}
              </a>
              {atLeastOnePost && (
                <div
                  style={{
                    display: 'flex',
                    justifyContent: 'flex-end',
                    marginRight: '5px',
                    fontWeight: 400,
                    fontSize: '13px',
                  }}
                >
                  {months[
                    new Date(topic.postsByTopicId.nodes[0].createdAt).getMonth()
                  ] +
                    ' ' +
                    new Date(topic.postsByTopicId.nodes[0].createdAt).getDate()}
                </div>
              )}
            </div>
          );
        })}
    </>
  );
};

export default ProfileTopics;
