interface CommentCreation {
  visible: boolean;
  onReply: (values: any) => void;
  onCancel: () => void;
}

const CommentForm = ({ visible, onReply, onCancel }: CommentCreation) => {
  const [form] = Form.useForm();
  const classes = useStyles();

  return (
    <Modal
      ariaHideApp={false}
      isOpen={visible}
      portalClassName={classes.portal}
      overlayClassName={{
        base: classes.overlayBase,
        afterOpen: classes.overlayAfterOpen,
        beforeClose: classes.overlayBeforeClose,
      }}
      className={classes.modal}
      shouldCloseOnOverlayClick={true}
      onRequestClose={onCancel}
      closeTimeoutMS={225}
    >
      <Form form={form} layout='vertical'>
        <div className={classes.header}>
          <div
            style={{
              display: 'flex',
              alignItems: 'center',
              flexDirection: 'row',
            }}
          >
            <div className={classes.title}>Comment</div>
            <div
              style={{
                minWidth: 60,
                display: 'flex',
                flexGrow: 1,
                justifyContent: 'flex-end',
              }}
            >
              <button className={classes.close} onClick={onCancel}>
                <span
                  style={{
                    width: '100%',
                    display: 'flex',
                    alignItems: 'inherit',
                    justifyContent: 'inherit',
                    fontSize: '1.5rem',
                  }}
                >
                  <IoMdClose />
                </span>
              </button>
            </div>
          </div>
        </div>
        <Space className={classes.content} direction='vertical' size={25}>
          <div style={{ padding: '0 24px' }}>
            <Form.Item name='comment'>
              <TextArea
                name='comment'
                placeholder='Type comment here...'
                autoSize={{ minRows: 4, maxRows: 20 }}
                className={classes.textField}
              />
            </Form.Item>
          </div>
        </Space>
        <div
          style={{
            display: 'flex',
            justifyContent: 'flex-end',
            paddingTop: '5px',
            paddingRight: '20px',
            paddingBottom: '20px',
          }}
        >
          <button
            onClick={() => {
              form
                .validateFields()
                .then((values) => {
                  form.resetFields();
                  onReply(values);
                })
                .catch((info) => {
                  console.log('Validate Failed:', info);
                });
            }}
          >
            Reply
          </button>
        </div>
      </Form>
    </Modal>
  );
};

function CommentButton(props: any) {
  const [visible, setVisible] = useState(false);
  const { loading, error, data } = useCurrentUserIdQuery();
  const [createComment] = useCreateComment();
  console.log(props.postId);
  console.log(data?.currentUserId);
  const onReply = async (values: any) => {
    try {
      console.log(props.postId);
      const newComment = await createComment({
        variables: {
          userId: data?.currentUserId,
          comment: values.comment,
          postId: props.postId,
        },
      });
      setVisible(false);
      if (newComment && newComment.data && newComment.data.createComment) {
        await props.refetch();
      }
    } catch (error) {
      console.error(error);
    }
  };
  return (
    <>
      <Button
        onClick={() => {
          setVisible(true);
        }}
      >
        Comment
      </Button>
      <CommentForm
        visible={visible}
        onReply={onReply}
        onCancel={() => {
          setVisible(false);
        }}
      />
    </>
  );
}

export default CommentButton;
