import React, { useState } from 'react';
import {
  Row,
  Col,
  Space,
  Typography,
  Button,
  Modal,
  Form,
  Input,
  Radio,
} from 'antd';
import { useCreateTopic, useCurrentUserId } from 'utils/services';

interface CreateTopicFormSettings {
  visible: boolean;
  onCreate: (values: Topic) => void;
  onCancel: (e: React.MouseEvent<HTMLElement, MouseEvent>) => void;
}

interface Topic {
  title: string;
  userId: any;
  notes?: string;
}

// Look in antd Form in Modal docs
const CreateTopicForm = ({
  visible,
  onCreate,
  onCancel,
}: CreateTopicFormSettings) => {
  const [form] = Form.useForm();
  return (
    <Modal
      visible={visible}
      title='Create a new topic'
      okText='Create'
      cancelText='Cancel'
      onCancel={onCancel}
      onOk={() => {
        form
          .validateFields()
          .then((values) => {
            form.resetFields();
            onCreate(values);
          })
          .catch((info) => {
            console.log('Validate Failed:', info);
          });
      }}
    >
      <Form form={form} layout='vertical'>
        <Form.Item
          name='title'
          label='Title'
          rules={[
            {
              required: true,
              message: 'Please enter a title for the topic!',
            },
          ]}
        >
          <Input />
        </Form.Item>
        <Form.Item name='notes' label='Notes'>
          <Input.TextArea
            showCount
            maxLength={200}
            autoSize={{ minRows: 3, maxRows: 6 }}
          />
        </Form.Item>
      </Form>
    </Modal>
  );
};

const CreateTopicPage = () => {
  const [visible, setVisible] = useState(false);
  const { loading, data, error } = useCurrentUserId();
  const [createTopic] = useCreateTopic();

  const onCreate = async (topic: Topic) => {
    try {
      const newTopic = await createTopic({
        variables: {
          title: topic.title,
          userId: data?.currentUserId,
          notes: topic.notes,
        },
      });
      setVisible(false);
      if (newTopic && newTopic.data && newTopic.data.createTopic) {
        window.location.href = `/${newTopic.data.createTopic.userByUserId?.username}/${newTopic.data.createTopic.topic?.title}`;
      }
    } catch (error) {
      console.error(error);
      setVisible(false);
    }
  };

  return (
    <>
      <Col span={14}>
        <Space direction='vertical'>
          <Typography.Title>Topics</Typography.Title>
          <Button
            type='primary'
            onClick={() => {
              setVisible(true);
            }}
          >
            New Topic
          </Button>
          <CreateTopicForm
            visible={visible}
            onCreate={onCreate}
            onCancel={() => {
              setVisible(false);
            }}
          />
        </Space>
      </Col>
      <Col flex={6}></Col>
    </>
  );
};

export default CreateTopicPage;
