import React from 'react';

import { ImHome3 } from 'react-icons/im';
import { ImSearch } from 'react-icons/im';
import { RiAdminFill } from 'react-icons/ri';
import { UserOutlined } from '@ant-design/icons';
// import MediaQuery from 'react-responsive';
import createIcon from 'assets/images/create.png';

import { Col, Avatar } from 'antd';
import { createUseStyles } from 'react-jss';
import { useCurrentUser, useCurrentUserRole } from 'utils/services';

const useStyles = createUseStyles({
  root: {
    '&:hover': {
      outline: 'none',
    },
    '&:focus': {
      outline: 'none',
    },
    fontFamily: ['Montserrat', 'sans-serif'],
    fontWeight: 600,
    color: '#FFFFFF',
    textDecoration: 'none',
    textTransform: 'none',
    fontSize: '20px',
    width: '270px',
    justifyContent: 'left',
  },
  noText: {
    '&:hover': {
      outline: 'none',
    },
    '&:focus': {
      outline: 'none',
    },
    color: '#3E4E56',
    justifyContent: 'center',
  },
  noTextIcon: {
    width: '50px',
    height: '50px',
  },
  button: {
    '&:hover': {
      outline: 'none',
      textDecoration: 'none',
      color: '#7699a8',
      backgroundColor: '#FFFFFF',
    },
    '&:focus': {
      outline: 'none',
    },
    fontFamily: ['Montserrat', 'sans-serif'],
    borderRadius: '15px',
    color: '#000000',
    fontSize: '20px',
    fontWeight: 700,
    height: '80px',
    padding: '10px 0',
    display: 'flex',
    alignItems: 'center',
  },
  icon: {
    width: '50px',
    height: '50px',
    marginRight: '20px',
  },
});

function LeftNavbar() {
  const { loading, error, data } = useCurrentUser();
  const role = useCurrentUserRole();

  const isAdmin = role.data?.currentUserRole === 'onecase_admin';

  const classes = useStyles();

  const username = data?.currentUser?.username;
  const profilePictureUrl = data?.currentUser?.profilePicture as string;

  if (loading || role.loading) {
    return null;
  }
  return (
    <React.Fragment>
      <Col span={4}>
        <div
          style={{
            display: 'flex',
            flexDirection: 'column',
            marginTop: '25vh',
            position: 'fixed',
          }}
        >
          <React.Fragment>
            {username && (
              <React.Fragment>
                <a href={'/home'} className={classes.button}>
                  <div>
                    <ImHome3 className={classes.icon} />
                    Home
                  </div>
                </a>
                {/* <a href={'/explore'} className={classes.button}>
                  <ImSearch className={classes.icon} />
                  Explore
                </a> */}
                <a href={'/create-topic'} className={classes.button}>
                  <img src={createIcon} alt='create' className={classes.icon} />
                  Create
                </a>
                <a href={'/' + username} className={classes.button}>
                  {!loading && (
                    <Avatar
                      style={{
                        marginRight: '20px',
                      }}
                      size={50}
                      src={profilePictureUrl}
                      icon={<UserOutlined />}
                    />
                  )}
                  Profile
                </a>
                {isAdmin && (
                  <a href={'/admin'} className={classes.button}>
                    <RiAdminFill className={classes.icon} />
                    Admin
                  </a>
                )}
              </React.Fragment>
            )}
          </React.Fragment>
        </div>
      </Col>
    </React.Fragment>
  );
}

export default LeftNavbar;
