import { createUseStyles } from 'react-jss';
import { useParams } from 'react-router-dom';

const useStyles = createUseStyles({
  button: {
    '&:hover': {
      outline: 'none',
      textDecoration: 'none',
      color: '#ABABAB',
    },
    '&:focus': {
      outline: 'none',
    },
    fontFamily: ['Montserrat', 'sans-serif'],
    backgroundColor: '#3E4E55',
    borderRadius: '15px',
    color: '#FFFFFF',
    fontSize: '20px',
    fontWeight: 800,
    minHeight: 50,
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    textAlign: 'center',
    boxShadow: 'none',
    margin: '100px auto 0',
    border: 'none',
  },
});

function EditPostButton(props: any) {
  const classes = useStyles();
  const { postId } = useParams<Params>();
  interface Params {
    postId: string;
  }
  return (
    <button
      className={classes.button}
      onClick={() => (window.location.href = '/p/' + postId + '/edit')}
    >
      Edit Post
    </button>
  );
}

export default EditPostButton;
