import React, { useState } from 'react';
import Modal from 'react-modal';
import createUseStyles from 'react-jss';
// import MediaQuery from 'react-responsive';
import { Avatar, Col, Dropdown, Menu } from 'antd';
import { EllipsisOutlined } from '@ant-design/icons';
// Components
import CreatePostButton from 'components/CreatePostButton';
import EditTopicButton from 'components/EditTopicButton';
import NotFoundPage from './NotFoundPage';
// Utils
import {
  useCurrentUser,
  useTopicByTitleAndUserId,
  useUserIdByUsername,
  useDeletePostByPostId,
} from 'utils/services';
import { useParams } from 'react-router-dom';

let months = [
  'Jan',
  'Feb',
  'Mar',
  'Apr',
  'May',
  'June',
  'July',
  'Aug',
  'Sep',
  'Oct',
  'Nov',
  'Dec',
];

interface Post {
  description: string;
  dateCreated: Date;
  content: Array<Content>;
}

interface Content {
  type: 'text' | 'image';
  order?: number;
  content?: string | Array<Content>;
  name?: string;
  url?: string;
}

function PostsPage() {
  const { username, topicTitle } = useParams<Params>();
  const [deletePost] = useDeletePostByPostId();
  const [confirmIsOpen, setConfirmIsOpen] = useState(false);
  const [selectedPost, setSelectedPost] = useState<Post>();

  const getUser = useUserIdByUsername({
    variables: {
      username,
    },
  });

  const currentUser = useCurrentUser();

  const user = getUser.data?.userByUsername;

  const topic = useTopicByTitleAndUserId({
    variables: {
      title: topicTitle,
      userId: user?.userId,
    },
  });

  if (!topic.loading && topic.data?.topicByTitleAndUserId === undefined) {
    return <NotFoundPage />;
  }

  let isOwner = false;
  if (
    getUser.data?.userByUsername?.userId ===
    currentUser.data?.currentUser?.userId
  ) {
    isOwner = true;
  }

  const posts = topic.data?.topicByTitleAndUserId?.postsByTopicId.nodes;

  interface Params {
    username: string;
    topicTitle: string;
  }

  const coverImage = topic.data?.topicByTitleAndUserId?.coverImage as string;

  const handleDelete = async (post: any) => {
    const deletedPost = await deletePost({
      variables: {
        postId: post?.postId,
      },
    });
    if (
      deletedPost &&
      deletedPost.data &&
      deletedPost.data.deletePostByPostId
    ) {
      topic.refetch();
    }
  };

  const postOptions = (post: any) => (
    <Menu>
      <Menu.Item>
        <a href={`/p/${post?.postId}/edit`}>Edit</a>
      </Menu.Item>
      <Menu.Item>
        <a
          onClick={() => {
            setSelectedPost(post);
            setTimeout(() => {
              setConfirmIsOpen(true);
            }, 500);
          }}
        >
          Delete
        </a>
      </Menu.Item>
    </Menu>
  );

  if (topic.loading) return null;

  return (
    <>
      <Col span={16}>
        <div
          style={{
            display: 'flex',
            width: '100%',
            backgroundColor: '#373E49',
            height: '200px',
            justifyContent: 'center',
            alignItems: 'flex-end',
          }}
        >
          <img style={{ width: '100%', height: '100%' }} src={coverImage}></img>
          {isOwner && (
            <EditTopicButton
              title={topicTitle}
              notes={topic.data?.topicByTitleAndUserId?.notes}
              coverImageUrl={topic.data?.topicByTitleAndUserId?.coverImage}
              topicColor={topic.data?.topicByTitleAndUserId?.topicColor}
              refetch={topic.refetch}
            />
          )}
        </div>
        <div
          style={{
            display: 'flex',
            width: '100%',
            justifyContent: 'center',
            alignItems: 'flex-end',
            marginTop: '-15px',
          }}
        >
          <div
            style={{
              color: '#FFFFFF',
              marginBottom: '12px',
              backgroundColor: topic.data?.topicByTitleAndUserId
                ?.topicColor as string,
              borderRadius: '5px',
              fontWeight: 700,
              fontSize: '24px',
              paddingTop: '4px',
              paddingBottom: '4px',
              paddingLeft: '10px',
              paddingRight: '10px',
            }}
          >
            {topic.data?.topicByTitleAndUserId?.title}
          </div>
        </div>
        <div
          style={{
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            marginTop: '15px',
          }}
        >
          {!topic.loading &&
            posts &&
            posts?.map((post: any) => {
              let date =
                months[new Date(post?.createdAt).getMonth()] +
                ' ' +
                new Date(post?.createdAt).getDate();
              let images = [];
              let content: Content[] = [];
              post?.content?.forEach((c: any) => {
                content.push(JSON.parse(c));
              });

              console.log(content);
              for (let current = 0; current < content.length - 1; current++) {
                if (images.length >= 2) {
                  break;
                } else if (content[current].type === 'image') {
                  if (content[current].content?.length === 1)
                    images.push({
                      name: (content[current].content?.[0] as Content).name,
                      url: (content[current].content?.[0] as Content).url,
                    });
                  else {
                    for (
                      let c = 0;
                      c < (content[current]?.content?.length as number) - 1;
                      c++
                    ) {
                      if (images.length >= 2) {
                        break;
                      } else {
                        images.push({
                          name: (content[current].content?.[c] as Content).name,
                          url: (content[current].content?.[c] as Content).url,
                        });
                      }
                    }
                  }
                }
              }

              return (
                <div
                  style={{
                    display: 'flex',
                    flexDirection: 'column',
                    backgroundColor: '#373E49',
                    borderRadius: '10px',
                    marginBottom: '15px',
                    color: 'white',
                    paddingTop: '10px',
                    paddingBottom: '7px',
                    paddingLeft: '15px',
                    paddingRight: '10px',
                  }}
                >
                  <div style={{ display: 'flex' }}>
                    <a
                      href={`/p/${post?.postId}`}
                      style={{ fontWeight: 700, marginBottom: '10px' }}
                    >
                      {post?.description}
                    </a>
                    <div
                      style={{
                        // display: 'flex',
                        // alignItems: 'center',
                        // justifyContent: 'center',
                        // alignSelf: 'center',
                        marginLeft: '10px',
                        // backgroundColor: 'white',
                        // color: 'black',
                        // height: '10px',
                      }}
                    >
                      {isOwner && (
                        <Dropdown
                          overlay={postOptions(post)}
                          trigger={['click']}
                        >
                          <a onClick={(e) => e.preventDefault()}>
                            <EllipsisOutlined />
                          </a>
                        </Dropdown>
                      )}
                    </div>
                  </div>
                  <div style={{ display: 'flex', maxWidth: '100%' }}>
                    {images.map((image) => {
                      return (
                        <div style={{ marginRight: '25px' }}>
                          <img
                            style={{ maxWidth: '90px' }}
                            src={image.url}
                            alt={image.name}
                          />
                        </div>
                      );
                    })}
                  </div>
                  <div
                    style={{
                      display: 'flex',
                      justifyContent: 'flex-end',
                      fontSize: '10px',
                      fontWeight: 400,
                      marginRight: '3px',
                      paddingTop: '3px',
                    }}
                  >
                    {date}
                  </div>
                </div>
              );
            })}
        </div>
      </Col>
      <Col span={4}>
        <div
          style={{
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >
          <div style={{ display: 'flex', marginTop: '50px' }}>
            <a href={`/${user?.username}`} style={{ display: 'block' }}>
              <Avatar src={user?.profilePicture} size={64} />
            </a>
            <div
              style={{
                display: 'flex',
                fontWeight: 400,
                fontSize: '24px',
                justifyContent: 'center',
                alignItems: 'center',
                marginLeft: '10px',
                flexDirection: 'column',
              }}
            >
              <a href={`/${user?.username}`} style={{ display: 'block' }}>
                {user?.name}
              </a>
              <span>@{user?.username}</span>
            </div>
          </div>
          <div style={{ marginBottom: '75px' }}>
            {isOwner && (
              <CreatePostButton
                topicId={topic.data?.topicByTitleAndUserId?.topicId}
                title={topic.data?.topicByTitleAndUserId?.title}
              />
            )}
          </div>
          <div
            style={{
              display: 'flex',
              flexDirection: 'column',
              alignItems: 'center',
              width: '100%',
            }}
          >
            <div
              style={{
                textAlign: 'center',
                fontWeight: 700,
                fontSize: '24px',
              }}
            >
              Notes
            </div>
            <div
              style={{
                width: '90%',
                backgroundColor: '#373E49',
                borderRadius: '8px',
                minHeight: '150px',
                color: 'white',
                paddingTop: '5px',
                paddingBottom: '5px',
                paddingLeft: '15px',
                paddingRight: '15px',
                fontWeight: 700,
                fontSize: '12px',
              }}
            >
              {topic.data?.topicByTitleAndUserId?.notes}
            </div>
          </div>
        </div>
        <Modal
          isOpen={confirmIsOpen}
          style={{
            overlay: {
              position: 'fixed',
              // top: '25%',
              // left: '50%',
              // marginRight: '-50%',
              // transform: 'translate(-50%,-50%)',
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'center',
              backgroundColor: 'rgba(0, 0, 0, 0.45)',
              zIndex: 1000,
              height: '100%',
            },
            content: {
              display: 'flex',
              flexDirection: 'column',
              top: '50%',
              left: '50%',
              marginRight: '-50%',
              transform: 'translate(-50%,-50%)',
              width: '30%',
              height: '20%',
            },
          }}
        >
          <p style={{ fontSize: '1vw', fontWeight: 700 }}>
            {' '}
            Are you sure you want to delete the following post?{' '}
          </p>
          <p style={{ textAlign: 'center', fontWeight: 500, fontSize: '1vw' }}>
            {selectedPost?.description}
          </p>
          <div
            style={{
              display: 'flex',
              flex: '1',
              justifyContent: 'flex-end',
              alignItems: 'flex-end',
            }}
          >
            <button
              style={{ marginRight: '20px' }}
              onClick={() => setConfirmIsOpen(false)}
            >
              No
            </button>
            <button
              onClick={() => {
                setConfirmIsOpen(false);
                handleDelete(selectedPost);
              }}
            >
              Yes
            </button>
          </div>
        </Modal>
      </Col>
    </>
  );
}

export default PostsPage;
