interface CommentCreation {
  visible: boolean;
  onReply: (values: any) => void;
  onCancel: () => void;
}

const { TextArea } = Input;

const CommentForm = ({ visible, onReply, onCancel }: CommentCreation) => {
  const [form] = Form.useForm();
  const classes = useStyles();

  return (
    <Modal
      ariaHideApp={false}
      isOpen={visible}
      portalClassName={classes.portal}
      overlayClassName={{
        base: classes.overlayBase,
        afterOpen: classes.overlayAfterOpen,
        beforeClose: classes.overlayBeforeClose,
      }}
      className={classes.modal}
      shouldCloseOnOverlayClick={true}
      onRequestClose={onCancel}
      closeTimeoutMS={225}
    >
      <Form form={form} layout='vertical'>
        <div className={classes.header}>
          <div
            style={{
              display: 'flex',
              alignItems: 'center',
              flexDirection: 'row',
            }}
          >
            <div className={classes.title}>Comment</div>
            <div
              style={{
                minWidth: 60,
                display: 'flex',
                flexGrow: 1,
                justifyContent: 'flex-end',
              }}
            >
              <button className={classes.close} onClick={onCancel}>
                <span
                  style={{
                    width: '100%',
                    display: 'flex',
                    alignItems: 'inherit',
                    justifyContent: 'inherit',
                    fontSize: '1.5rem',
                  }}
                >
                  <IoMdClose />
                </span>
              </button>
            </div>
          </div>
        </div>
        <Space className={classes.content} direction='vertical' size={25}>
          <div style={{ padding: '0 24px' }}>
            <Form.Item name='comment'>
              <TextArea
                name='comment'
                placeholder='Type comment here...'
                autoSize={{ minRows: 4, maxRows: 20 }}
                className={classes.textField}
              />
            </Form.Item>
          </div>
        </Space>
        <div
          style={{
            display: 'flex',
            justifyContent: 'flex-end',
            paddingTop: '5px',
            paddingRight: '20px',
            paddingBottom: '20px',
          }}
        >
          <button
            onClick={() => {
              form
                .validateFields()
                .then((values) => {
                  form.resetFields();
                  onReply(values);
                })
                .catch((info) => {
                  console.log('Validate Failed:', info);
                });
            }}
          >
            Reply
          </button>
        </div>
      </Form>
    </Modal>
  );
};

function Comment(props: any) {
  const classes = useStyles();
  const [visible, setVisible] = useState(false);
  const { loading, error, data } = useCurrentUserId();
  const [createComment] = useCreateComment();
  const [createLike] = useCreateLike();
  const [commentId, setCommentId] = useState();
  const [updateHasLike] = useUpdateHasLikedByLikeId();

  useEffect(() => {
    setCommentId(props.comment?.commentId);
  }, [props.comment?.commentId]);

  const checkLike = useHasLikedByUserIdAndCommentId({
    variables: {
      userId: data?.currentUserId,
      commentId,
    },
  });

  const [liked, setLiked] = useState(
    checkLike.data?.allLikes?.nodes.length === 0
      ? false
      : checkLike.data?.allLikes?.nodes[0]?.isLiked
  );
  useEffect(() => {
    if (checkLike.loading === false && checkLike.data) {
      setLiked(checkLike.data?.allLikes?.nodes[0]?.isLiked);
    }
  }, [checkLike.loading, checkLike.data]);

  const [likes, setLikes] = useState(props.comment.likes);
  const [updateLikes] = useUpdateLikesByCommentId();

  let userId = props.comment?.userId;
  let getUser = useGetUserByUserId({
    variables: {
      userId,
    },
  });
  const username = getUser?.data?.userByUserId?.username;
  const profilePicture = getUser?.data?.userByUserId?.profilePicture;

  let parentCommentId = props.comment?.commentId;
  let replies = useRepliesByParentCommentId({
    variables: {
      parentCommentId,
    },
  });
  const onLike = async () => {
    let increment = !liked ? 1 : -1;
    let newLikes = likes + increment;
    setLikes(newLikes);
    await updateLikes({
      variables: {
        commentId: props.comment.commentId,
        likes: newLikes,
      },
    });
    if (checkLike.data?.allLikes?.nodes.length === 0) {
      const createdLiked = await createLike({
        variables: {
          userId: data?.currentUserId,
          commentId: parentCommentId,
          isLiked: true,
        },
      });
      if (createdLiked && createdLiked.data && createdLiked.data.createLike) {
        await checkLike.refetch();
      }
    } else {
      let likeId = checkLike.data?.allLikes?.nodes[0]?.likeId;
      await updateHasLike({
        variables: {
          likeId,
          isLiked: !liked as boolean,
        },
      });
    }
  };

  const onReply = async (values: any) => {
    try {
      const newComment = await createComment({
        variables: {
          userId: data?.currentUserId,
          comment: values.comment,
          postId: props.postId,
          parentCommentId,
        },
      });
      setVisible(false);
      if (newComment && newComment.data && newComment.data.createComment) {
        await replies.refetch();
      }
    } catch (error) {
      console.error(error);
    }
  };

  return (
    <div
      style={{ marginTop: '20px', marginBottom: '20px' }}
      className={props.isReply ? classes.reply : classes.comment}
    >
      <a href={`/${username}`}>
        <Avatar src={profilePicture} icon={<UserOutlined />} />
      </a>

      <a href={`/${username}`} style={{ marginLeft: '5px' }}>
        {username}
      </a>
      <div>{props.comment.comment}</div>
      <div style={{ display: 'flex' }}>
        <div>
          {likes}
          <Button
            className={!liked ? classes.like : classes.liked}
            shape='circle'
            icon={!liked ? <LikeOutlined /> : <LikeFilled />}
            onClick={() => {
              setLiked(!liked);
              onLike();
            }}
          ></Button>
        </div>
        <Button
          onClick={() => {
            setVisible(true);
          }}
        >
          Reply
        </Button>
      </div>
      <CommentForm
        visible={visible}
        onReply={onReply}
        onCancel={() => {
          setVisible(false);
        }}
      />
      <div>
        {replies?.data?.allComments?.nodes?.map((reply) => {
          return (
            <Comment comment={reply} postId={props.postId} isReply={true} />
          );
        })}
      </div>
    </div>
  );
}

export default Comment;
