import React from 'react';
// import MediaQuery from 'react-responsive';
import { Avatar, Col, Button } from 'antd';
import { UserOutlined } from '@ant-design/icons';
// Components
import CreatePostButton from 'components/CreatePostButton';
import Carousel from 'components/Carousel';
import Comment from 'components/Comment';
import CommentButton from 'components/CommentButton';
import EditPostButton from 'components/EditPostButton';

// Utils
import { createUseStyles } from 'react-jss';
import { useParams, useHistory } from 'react-router-dom';
import {
  useCurrentPostCursor,
  useCurrentUser,
  useNextPost,
  usePostByPostId,
  usePreviousPost,
  useCommentByPostId,
} from 'utils/services';

const useStyles = createUseStyles({
  root: {
    margin: '80px auto 0 auto',
    display: 'flex',
    justifyContent: 'center',
    padding: 0,
  },
  journalTitle: {
    textAlign: 'left',
    paddingLeft: '15px',
    paddingTop: '8px',
    paddingBottom: '7px',
    fontFamily: ['Open Sans', 'sans-serif'],
    fontWeight: 'bolder',
    color: 'white',
    width: '100%',
    backgroundColor: '#404040',
    // borderWidth: '30px',
    borderRadius: '10px',
  },
  journalContent: {
    marginTop: '25px',
    padding: '15px',
    backgroundColor: '#FFFFFF',
    width: '100%',
    minHeight: '75vh',
    borderRadius: '10px',
    marginBottom: '50px',
  },
  pfp: {
    marginRight: '10px',
  },
  entryHeader: {
    display: 'flex',
    marginBottom: '15px',
  },
  entryText: {
    fontFamily: ['Open Sans', 'sans-serif'],
    color: '#3D3D3D',
    marginBottom: '10px',
  },
  description: {},
});

var months = [
  'Jan',
  'Feb',
  'Mar',
  'Apr',
  'May',
  'June',
  'July',
  'Aug',
  'Sep',
  'Oct',
  'Nov',
  'Dec',
];

function PostViewPage() {
  const classes = useStyles();
  const history = useHistory();

  const { postId } = useParams<Params>();
  interface Params {
    postId: string;
  }

  const { loading, error, data } = usePostByPostId({
    variables: {
      postId,
    },
  });

  const currentUser = useCurrentUser();

  const cursor = useCurrentPostCursor({
    variables: {
      topicId: data?.postByPostId?.topicByTopicId?.topicId,
      postId,
    },
  });

  const previousPost = usePreviousPost({
    variables: {
      topicId: data?.postByPostId?.topicByTopicId?.topicId,
      before: cursor.data?.topicByTopicId?.postsByTopicId.edges[0].cursor,
    },
  });
  const nextPost = useNextPost({
    variables: {
      topicId: data?.postByPostId?.topicByTopicId?.topicId,
      after: cursor.data?.topicByTopicId?.postsByTopicId.edges[0].cursor,
    },
  });

  let isOwner = false;
  if (
    data?.postByPostId?.userByUserId?.userId ===
    currentUser.data?.currentUser?.userId
  ) {
    isOwner = true;
  }

  let previousPostId: any = null;
  let nextPostId: any = null;
  if (
    (previousPost.data?.topicByTopicId?.postsByTopicId.edges || []).length > 0
  ) {
    previousPostId =
      previousPost.data?.topicByTopicId?.postsByTopicId.edges[0].node?.postId;
  }
  if ((nextPost.data?.topicByTopicId?.postsByTopicId.edges || []).length > 0) {
    nextPostId =
      nextPost.data?.topicByTopicId?.postsByTopicId.edges[0].node?.postId;
  }

  const hasPreviousPost = previousPostId ? true : false;

  const hasNextPost = nextPostId ? true : false;

  const user = data?.postByPostId?.userByUserId;
  const title = data?.postByPostId?.topicByTopicId?.title;
  const description = data?.postByPostId?.description;
  const createdAt = new Date(data?.postByPostId?.createdAt);
  const date = months[createdAt.getMonth()] + ' ' + createdAt.getDate();
  let contents: any[] = [];
  data?.postByPostId?.content?.forEach((content: any) => {
    contents.push(JSON.parse(content));
  });

  const comments = useCommentByPostId({
    variables: {
      postId,
    },
  });

  interface Content {
    type: 'text' | 'image';
    order?: number;
    content?: string | Image[];
  }

  type Image = {
    name: string;
    url: string;
  };
  if (loading || cursor.loading || previousPost.loading || nextPost.loading)
    return null;
  return (
    <>
      <Col span={2}>
        {hasPreviousPost && (
          <Button onClick={() => history.push(`/p/${previousPostId}`)}>
            Previous Post
          </Button>
        )}
      </Col>
      <Col span={12}>
        <div>
          {!loading && (
            <React.Fragment>
              <div
                style={{
                  display: 'flex',
                  justifyContent: 'flex-end',
                  marginTop: '5px',
                  marginBottom: '5px',
                }}
              >
                {date}
              </div>
              <div className={classes.journalTitle}>{title}</div>
              <div className={classes.journalContent}>
                <div className={classes.entryHeader}>
                  <a href={`/${user?.username}`} style={{ display: 'block' }}>
                    <Avatar
                      size={64}
                      src={user?.profilePicture}
                      icon={<UserOutlined />}
                    />
                  </a>
                  <div
                    style={{
                      display: 'flex',
                      fontWeight: 400,
                      fontSize: '24px',
                      justifyContent: 'center',
                      alignItems: 'center',
                      marginLeft: '10px',
                      flexDirection: 'column',
                    }}
                  >
                    <a href={`/${user?.username}`} style={{ display: 'block' }}>
                      {user?.name}
                    </a>
                    <span>@{user?.username}</span>
                  </div>
                </div>
                {contents.map((content) => {
                  if (content.type === 'text') {
                    return (
                      <div className={classes.entryText}>{content.content}</div>
                    );
                  } else if (content.type === 'image') {
                    let images: string[] = [];
                    (content.content as Image[]).forEach((content) => {
                      images.push(content.url);
                    });
                    return (
                      <div>
                        <Carousel images={images}></Carousel>
                      </div>
                    );
                  }
                })}
              </div>
            </React.Fragment>
          )}
        </div>
        <div>
          <CommentButton
            refetch={() => {
              comments.refetch();
            }}
            postId={postId}
          />
          <div>Comments:</div>
          <div>
            {comments.data?.allComments?.nodes?.map((comment: any) => {
              return (
                <Comment
                  refetch={() => {
                    comments.refetch();
                  }}
                  comment={comment}
                  postId={postId}
                  isReply={false}
                />
              );
            })}
          </div>
        </div>
      </Col>

      <Col span={2}>
        {hasNextPost && (
          <Button onClick={() => history.push(`/p/${nextPostId}`)}>
            Next Post
          </Button>
        )}
      </Col>
      <Col span={4}>
        {isOwner && (
          <>
            <CreatePostButton
              topicId={data?.postByPostId?.topicByTopicId?.topicId}
              title={data?.postByPostId?.topicByTopicId?.title}
            />
            <EditPostButton />
          </>
        )}

        <div
          style={{
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            width: '100%',
          }}
        >
          <div
            style={{
              textAlign: 'center',
              fontWeight: 700,
              fontSize: '24px',
            }}
          >
            Description
          </div>
          <div
            style={{
              width: '90%',
              backgroundColor: '#373E49',
              borderRadius: '8px',
              minHeight: '150px',
              color: 'white',
              paddingTop: '5px',
              paddingBottom: '5px',
              paddingLeft: '15px',
              paddingRight: '15px',
              fontWeight: 700,
              fontSize: '12px',
            }}
          >
            {description}
          </div>
        </div>
      </Col>
    </>
  );
}

export default PostViewPage;
