import React from 'react';
import {
  BrowserRouter as Router,
  Route,
  Switch,
  useLocation,
} from 'react-router-dom';
import { createUseStyles } from 'react-jss';

import client from 'utils/ApolloClient';
import { ApolloProvider } from '@apollo/client';

// Antd
import { Layout, Row } from 'antd';

import 'antd/dist/antd.css';
import SideNav from 'components/SideNav';
import TopNav from 'components/TopNav';
// Pages
import HomePage from 'pages/HomePage';
import CreatePostPage from 'pages/CreatePostPage';
import CreateTopicPage from 'pages/CreateTopicPage';
import EditPostPage from 'pages/EditPostPage';
import ProfilePage from 'pages/ProfilePage';
import PostViewPage from 'pages/PostViewPage';
import TopicViewPage from 'pages/TopicViewPage';
import LandingPage from 'pages/LandingPage';
import AdminPage from 'pages/AdminPage';
import NotFoundPage from 'pages/NotFoundPage';
import PrivateRoute from 'components/PrivateRoute';
import PublicRoute from 'components/PublicRoute';
import AdminRoute from 'components/AdminRoute';

import '@uppy/core/dist/style.css';
import '@uppy/dashboard/dist/style.css';
import SearchResultsPage from 'pages/SearchResultsPage';

const { Header, Content } = Layout;
const useStyles = createUseStyles({
  screen: {
    margin: '0 auto',
    maxWidth: '1460px',

    '@media screen and (min-width:1024px) and (max-width:1400px)': {
      maxWidth: '1000px',
    },
    '@media screen and (min-width:768px) and (max-width:1023px)': {
      maxWidth: '800px',
    },
    '@media screen and (max-width:767px)': {
      maxWidth: '700px',
    },
    '@media screen and (max-width:480px)': {
      maxWidth: '480px',
    },
  },
});

const MainContent = () => {
  const classes = useStyles();
  return (
    <Row className={classes.screen}>
      <SideNav />

      <Switch>
        <PrivateRoute exact path='/home' component={HomePage} />
        <PrivateRoute exact path='/create-topic' component={CreateTopicPage} />
        <PrivateRoute exact path='/create-post' component={CreatePostPage} />
        <AdminRoute exact path='/admin' component={AdminPage} />
        <PublicRoute exact path='/search' component={SearchResultsPage} />
        <PublicRoute exact path='/p/:postId' component={PostViewPage} />
        <PrivateRoute exact path='/p/:postId/edit' component={EditPostPage} />
        <PublicRoute
          exact
          path='/:username/:topicTitle'
          component={TopicViewPage}
        />
        <PublicRoute exact path='/:username' component={ProfilePage} />
        <PublicRoute component={NotFoundPage} />
      </Switch>
    </Row>
  );
};
function App() {
  const classes = useStyles();
  return (
    <ApolloProvider client={client}>
      <Router>
        <Layout>
          <Header style={{ backgroundColor: '#3e4e55' }}>
            <TopNav />
          </Header>
          <Switch>
            <PublicRoute
              restricted={true}
              exact
              path='/'
              component={LandingPage}
            />
            <Route>
              <Content style={{ minHeight: '100vh' }}>
                <MainContent />
              </Content>
            </Route>
          </Switch>
        </Layout>
      </Router>
    </ApolloProvider>
  );
}

export default App;
