import React, { useEffect, useState, useRef } from 'react';
// Antd + Styling
import { Col, Input, Upload, Button, Space, Row } from 'antd';
import { RcFile } from 'antd/lib/upload';
import { createUseStyles } from 'react-jss';
import Creatable from 'react-select/creatable';
import TextareaAutoSize from 'react-textarea-autosize';
// Utils
import {
  useCreatePost,
  useCreateTopic,
  useCurrentUserId,
  useTopicsByUserId,
} from 'utils/services';
import { useLocation } from 'react-router-dom';
import { TopicsByUserIdDocument } from 'generated/graphql';
import isProd from 'utils/isDev';

const useStyles = createUseStyles({
  root: {
    margin: '80px auto 0 auto',
    minWidth: '1250px',
    maxWidth: '1250px',
    display: 'flex',
    padding: 0,
  },
  container: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
  },
  header: {
    textTransform: 'none',
    fontFamily: ['Montserrat', 'sans-serif'],
    backgroundColor: '#000000',
    color: '#FFFFFF',
    fontSize: '24px',
    fontWeight: 800,
    borderRadius: '15px',
    alignSelf: 'center',
    width: '50%',
    textAlign: 'center',
    margin: '10px',
    boxShadow: 'none',
  },
  textarea: {
    overflow: 'hidden',
    border: 'none',
    borderRadius: '8px',
    resize: 'none',
    display: 'block',
    padding: '4px 11px',
    width: '100%',
    '&::placeholder': {
      color: '#bfbfbf',
    },
    '&:focus': {
      outline: 'none',
    },
  },
  button: {
    display: 'flex',
    justifyContent: 'center',
  },
  hoverable: {
    '&:hover $upload': {
      display: 'flex',
      justifyContent: 'center',
    },
    height: '32px',
  },
  multipleImages: {
    '&:hover $upload': {
      display: 'flex',
      width: '100%',
      height: '100%',
      alignItems: 'center',
      justifyContent: 'center',
    },
    display: 'inline-block',
    width: '100%',
    height: '100%',
  },
  upload: {
    display: 'none',
  },
});

interface TopicState {
  topicId: string;
  title: string;
}

function CreatePostPage() {
  const classes = useStyles();

  const location = useLocation<TopicState>();

  const [topic, setTopic] = useState<Topic>();

  const [description, setDescription] = useState('');

  const [index, setIndex] = useState(0);

  const [numberOfTextAreas, setNumberOfTextAreas] = useState(1);

  const [contents, setContents] = useState<Array<Content>>([
    { type: 'text', content: '' },
  ]);
  const [files, setFiles] = useState<IndexedFile[]>([]);

  const [createTopic] = useCreateTopic();
  const [createPost] = useCreatePost();
  const userId = useCurrentUserId();
  const topics = useTopicsByUserId({ notifyOnNetworkStatusChange: true });

  interface Topic {
    value: string;
    label: string;
  }

  interface IndexedFile {
    i: number;
    j: number;
    file: RcFile;
  }

  let existingTopics: Topic[] = [];
  topics.data?.currentUser?.topicsByUserId.nodes.forEach((topic) => {
    const newOption: Topic = {
      value: topic?.topicId as string,
      label: topic?.title as string,
    };
    existingTopics.push(newOption);
  });

  let options = existingTopics;

  const refs: any = useRef([React.createRef()]);

  interface Content {
    type: 'text' | 'image';
    order?: number;
    content?: string | Image[];
  }

  type Image = {
    name: string;
    url: string;
  };

  useEffect(() => {
    refs.current[index].current.focus();
  }, [numberOfTextAreas]);

  useEffect(() => {
    if (location.state.topicId && location.state.title) {
      setTopic({
        value: location.state.topicId,
        label: location.state.title,
      });
    }
  }, [location.state]);

  const handleCreateTopic = async (inputValue: string) => {
    try {
      const newTopic = await createTopic({
        variables: {
          title: inputValue,
          userId: userId.data?.currentUserId,
        },
        refetchQueries: [{ query: TopicsByUserIdDocument }],
      });

      const newOption = {
        value: newTopic.data?.createTopic?.topic?.topicId,
        label: inputValue,
      };

      if (newTopic && newTopic.data && newTopic.data.createTopic) {
        setTopic(newOption);
        topics.refetch();
      }
    } catch (error) {
      console.error(error);
    }
  };

  const handleTextChange = (
    e: React.ChangeEvent<HTMLTextAreaElement>,
    index: number
  ) => {
    const list = [...contents];
    list[index].content = e.target.value;
    setContents(list);
  };

  const handleKeyDown = (
    e: React.KeyboardEvent<HTMLTextAreaElement>,
    i: number
  ) => {
    if (e.key === 'Enter') {
      e.preventDefault(); // prevent new line in a text area

      setIndex(i + 1); // keep track of input index
      // Create a new text area instead
      const list = [...contents];
      let newTextArea: Content = {
        type: 'text',
        content: '',
      };
      list.splice(i + 1, 0, newTextArea);
      refs.current = [...Array(list.length)].map((item) => React.createRef());
      setContents(list);
      setNumberOfTextAreas(numberOfTextAreas + 1);
    } else if (
      (e.key === 'Backspace' || e.key === 'Delete') &&
      i !== 0 &&
      contents[i - 1].type !== 'image' &&
      contents[i].content === ''
    ) {
      e.preventDefault();
      const list = [...contents];
      setIndex(i - 1);
      list.splice(i, 1);
      refs.current = [...Array(list.length)].map((item) => React.createRef());
      setContents(list);
      setNumberOfTextAreas(numberOfTextAreas - 1);
    }
  };

  const handleTopicSelect = (topic: any) => {
    setTopic(topic);
  };

  const handleUpload = (file: RcFile, i: number) => {
    if (file.size > 5242880) {
      alert('5 MB file size limit exceeded!');
      return false;
    }
    const newFile = {
      i: i + 1,
      j: 0,
      file,
    };
    setFiles([...files, newFile]);
    let url = URL.createObjectURL(file); // create the URL for the new image preview
    const list = [...contents];
    let newImage: Content = {
      type: 'image',
      content: [
        {
          name: file.name,
          url,
        },
      ],
    };
    list.splice(i + 1, 0, newImage);

    // Add new text area if the image is the last in the journal
    if (i === contents.length - 1) {
      let newTextArea: Content = {
        type: 'text',
        content: '',
      };
      list.splice(i + 2, 0, newTextArea);
      refs.current = [...Array(list.length)].map((item) => React.createRef());
    }
    setContents(list);
    return true;
  };
  interface Indices {
    i: number;
    j: number;
  }

  const handleDelete = ({ i, j }: Indices) => {
    const list = [...contents];
    let imageArray = list[i].content as Image[];
    imageArray.splice(j, 1);
    if (imageArray.length === 0) {
      list.splice(i, 1);
    }
    setContents(list);
  };

  const handleSlidableImages = (file: RcFile, i: number) => {
    if (file.size > 5242880) {
      alert('5 MB file size limit exceeded!');
      return false;
    }
    const list = [...contents];
    const newFile = {
      i,
      j: list[i].content?.length as number,
      file,
    };
    setFiles([...files, newFile]);

    if (Array.isArray(list[i].content)) {
      let url = URL.createObjectURL(file);
      let newImage: Image = {
        name: file.name,
        url,
      };
      (list[i].content as Image[]).push(newImage);
    }
    setContents(list);

    return true;
  };

  const handleSubmit = async () => {
    try {
      if (topic?.value === undefined) {
        alert('Topic cannot be empty!');
        return;
      }
      if (description === '') {
        alert('Description cannot be empty!');
        return;
      }

      // Create form data for multi-part/form spec
      let formData = new FormData();
      files.forEach((file) => {
        console.log(file);
        formData.append('images', file.file, file.file.name);
      });
      const uploadUrl = isProd() ? '/upload' : 'http://localhost:8080/upload';
      // Send POST request to store images in s3 and generate urls
      const response = await fetch(uploadUrl, {
        method: 'POST',
        body: formData,
      });
      const data = await response.json();
      // Save newly generated urls in contents json
      const newContents = [...contents];
      files.forEach((file, i) => {
        (newContents[file.i].content as Image[])[file.j].url = data[i];
      });

      let array: any[] = [];
      newContents.forEach((content) => {
        array.push(JSON.stringify(content));
      });

      const newPost = await createPost({
        variables: {
          description,
          userId: userId.data?.currentUserId,
          topicId: topic?.value,
          content: array,
        },
      });
      if (newPost && newPost.data && newPost.data.createPost) {
        window.location.href = `/p/${newPost.data.createPost.post?.postId}`;
      }
    } catch (error) {
      console.error(error);
    }
  };

  return (
    <>
      <Col span={16}>
        <div style={{ display: 'flex', justifyContent: 'center' }}>
          <div className={classes.header}>New Post</div>
        </div>
        <div>
          <Space direction='vertical' style={{ width: '100%' }}>
            <Creatable
              onChange={handleTopicSelect}
              onCreateOption={handleCreateTopic}
              options={options}
              value={topic}
            />
            <Input
              placeholder='Description'
              onChange={(e) => setDescription(e.target.value)}
            />
          </Space>

          <div
            style={{
              border: '3px solid black',
              borderRadius: '8px',
              margin: '8px 0',
              minHeight: '600px',
              backgroundColor: '#FFFFFF',
            }}
          >
            {contents.map((content, i) => {
              if (content.type === 'text') {
                if (contents[i] === contents[contents.length - 1]) {
                  return (
                    <>
                      <TextareaAutoSize
                        autoFocus={true}
                        placeholder='Type here'
                        className={classes.textarea}
                        value={contents[i].content as string}
                        ref={refs.current[i]}
                        onChange={(e) => handleTextChange(e, i)}
                        onKeyDown={(e) => handleKeyDown(e, i)}
                      />
                      <Upload
                        showUploadList={false}
                        className={classes.button}
                        beforeUpload={(file) => handleUpload(file, i)}
                      >
                        <Button>Add Image</Button>
                      </Upload>
                    </>
                  );
                } else if (
                  contents[i + 1] !== undefined &&
                  contents[i + 1].type === 'text'
                ) {
                  return (
                    <>
                      <TextareaAutoSize
                        autoFocus={true}
                        placeholder='Type here'
                        className={classes.textarea}
                        value={contents[i].content as string}
                        ref={refs.current[i]}
                        onChange={(e) => handleTextChange(e, i)}
                        onKeyDown={(e) => handleKeyDown(e, i)}
                      />
                      <div className={classes.hoverable}>
                        <Upload
                          showUploadList={false}
                          className={classes.upload}
                          beforeUpload={(file) => handleUpload(file, i)}
                        >
                          <Button>Add Image</Button>
                        </Upload>
                      </div>
                    </>
                  );
                } else {
                  return (
                    <>
                      <TextareaAutoSize
                        autoFocus={true}
                        placeholder='Type here'
                        className={classes.textarea}
                        value={contents[i].content as string}
                        ref={refs.current[i]}
                        onChange={(e) => handleTextChange(e, i)}
                        onKeyDown={(e) => handleKeyDown(e, i)}
                      />
                    </>
                  );
                }
              } else {
                return (
                  <Row gutter={8}>
                    {(content.content as Image[]).map((image, j) => {
                      return (
                        <Col span={4}>
                          <img src={image.url} width='100%' />
                          <Button
                            style={{
                              position: 'absolute',
                              top: '5px',
                              right: '5px',
                            }}
                            onClick={(e) => handleDelete({ i, j })}
                          >
                            Delete
                          </Button>
                        </Col>
                      );
                    })}
                    {(content.content as Image[]).length < 5 && (
                      <Col flex='auto'>
                        <div className={classes.multipleImages}>
                          <Upload
                            showUploadList={false}
                            className={classes.upload}
                            beforeUpload={(file) =>
                              handleSlidableImages(file, i)
                            }
                          >
                            <Button>Add Image</Button>
                          </Upload>
                        </div>
                      </Col>
                    )}
                  </Row>
                );
              }
            })}
          </div>
          <Button
            style={{
              backgroundColor: '#08e1ae',
              color: 'white',
              backgroundImage:
                'linear-gradient(315deg, #08e1ae 0%, #98de5b 74%)',
            }}
            onClick={handleSubmit}
          >
            Submit
          </Button>
        </div>
      </Col>
      <Col flex={4}></Col>
    </>
  );
}

export default CreatePostPage;
