const AdminPage = () => {
  const classes = useStyles();
  const [dataProvider, setDataProvider] = useState<LegacyDataProvider>();
  const [displayRegister, setDisplayRegister] = useState(false);
  const [displayUsers, setDisplayUsers] = useState(false);
  const [displayPosts, setDisplayPosts] = useState(false);
  const [usersPagination, setUsersPagination] = useState({
    current: 1,
    defaultPageSize: 10,
    pageSizeOption: ['5', '10', '15', '20', '25', '30'],
    showSizeChanger: true,
    locale: { items_per_page: '' },
  });

  const [postsPagination, setPostsPagination] = useState({
    current: 1,
    defaultPageSize: 5,
    pageSizeOption: ['5', '10', '15', '20', '25', '30'],
    showSizeChanger: true,
    locale: { items_per_page: '' },
  });
  const [deletePost] = useDeletePostByPostId();
  const [updateUser] = useUpdateUserByUserId();
  const [deleteUser] = useDeleteUserByUserId();
  const [searchText, setSearchText] = useState('');
  const [searchedColumn, setSearchedColumn] = useState('');
  const [searchInput, setSearchInput] = useState<Input>();

  const client = useApolloClient();

  const users = useAllUsers();
  const posts = useAllPosts();

  interface User {
    userId: string;
    name: string;
    username: string;
    createdAt: string;
    updatedAt: string;
    accountVerified: boolean;
  }

  interface Content {
    type: 'text' | 'image';
    order?: number;
    content?: string | Image[];
  }

  type Image = {
    name: string;
    url: string;
  };

  interface Post {
    postId: string;
    content: Content[];
    description: string;
    createdAt: string;
    updatedAt: string;
  }

  const onRegister = () => {
    setDisplayUsers(false);
    setDisplayPosts(false);
    setDisplayRegister(true);
  };

  const onUsers = () => {
    setDisplayPosts(false);
    setDisplayRegister(false);
    users.refetch();
    setDisplayUsers(true);
  };

  const onPosts = () => {
    setDisplayRegister(false);
    setDisplayUsers(false);
    posts.refetch();
    setDisplayPosts(true);
  };

  const handleUsersChange = (pagination: any, filters: any, sorter: any) => {
    setUsersPagination(pagination);
  };
  const handlePostsChange = (pagination: any, filters: any, sorter: any) => {
    setPostsPagination(pagination);
  };

  const handleDeleteUser = async (userId: any) => {
    const deletedUser = await deleteUser({
      variables: {
        userId,
      },
    });
    if (
      deletedUser &&
      deletedUser.data &&
      deletedUser.data.deleteUserByUserId
    ) {
      users.refetch();
    }
  };
  const handleVerifyUser = async (userId: any, verified: boolean) => {
    const updatedUser = await updateUser({
      variables: {
        input: {
          userId,
          userPatch: {
            accountVerified: !verified,
            updatedAt: new Date(
              Date.now() + 1000 * 60 * -new Date().getTimezoneOffset()
            )
              .toISOString()
              .replace('T', ' ')
              .replace('Z', ''),
          },
        },
      },
    });
    if (
      updatedUser &&
      updatedUser.data &&
      updatedUser.data.updateUserByUserId
    ) {
      users.refetch();
    }
  };
  const handleDeletePost = async (postId: any) => {
    try {
      const deletedPost = await deletePost({
        variables: {
          postId,
        },
      });
      if (
        deletedPost &&
        deletedPost.data &&
        deletedPost.data.deletePostByPostId
      ) {
        posts.refetch();
      }
    } catch (error) {
      console.error(error);
    }
  };

  const handleSearch = (selectedKeys: any, confirm: any, dataIndex: any) => {
    confirm();
    setSearchText(selectedKeys[0]);
    setSearchedColumn(dataIndex);
  };

  const handleReset = (clearFilters: any) => {
    clearFilters();
    setSearchText('');
  };

  type FormInputs = {
    setSelectedKeys: any;
    selectedKeys: any;
    confirm: any;
    clearFilters: any;
  };

  const getColumnSearchProps = (dataIndex: any) => ({
    filterDropdown: ({
      setSelectedKeys,
      selectedKeys,
      confirm,
      clearFilters,
    }: FormInputs) => (
      <div>
        <Input
          ref={(node) => {
            setSearchInput(node as Input);
          }}
          placeholder={`Search ${dataIndex}`}
          value={selectedKeys[0]}
          onChange={(e) =>
            setSelectedKeys(e.target.value ? [e.target.value] : [])
          }
          onPressEnter={() => handleSearch(selectedKeys, confirm, dataIndex)}
          style={{ width: 188, marginBottom: 8, display: 'block' }}
        />
        <Space>
          <Button
            type='primary'
            onClick={() => handleSearch(selectedKeys, confirm, dataIndex)}
            icon={<SearchOutlined />}
            size='small'
            style={{ width: 90 }}
          >
            Search
          </Button>
          <Button
            onClick={() => handleReset(clearFilters)}
            size='small'
            style={{ width: 90 }}
          >
            Reset
          </Button>
        </Space>
      </div>
    ),
    filterIcon: (filtered: any) => (
      <SearchOutlined style={{ color: filtered ? '#1890ff' : undefined }} />
    ),
    onFilter: (value: any, record: any) =>
      record[dataIndex]
        ? record[dataIndex]
            .toString()
            .toLowerCase()
            .includes(value.toLowerCase())
        : '',
    onFilterDropdownVisibleChange: (visible: any) => {
      if (visible) {
        // setTimeout(() => (searchInput as Input).select(), 100);
      }
    },
    render: (text: any) =>
      searchedColumn === dataIndex ? (
        <Highlighter
          highlightStyle={{ backgroundColor: '#ffc069', padding: 0 }}
          searchWords={[searchText]}
          autoEscape
          textToHighlight={text ? text.toString() : ''}
        />
      ) : (
        text
      ),
  });

  // const pgDataProviderConfig: ProviderOptions = {
  //   typeMap: {
  //     Post: {
  //       expand: true,
  //     },
  //   },
  // };
  // useEffect(() => {
  //   (async () => {
  //     // const dataProvider = await createDataProvider()
  //     // const dataProvider = await pgDataProvider(
  //     //   client as any,
  //     //   pgDataProviderConfig
  //     // );
  //     const dataProvider = await buildGraphQLProvider({
  //       client,
  //       introspection: { schema },
  //       buildQuery,
  //     });
  //     console.log(dataProvider);
  //     setDataProvider(() => dataProvider);
  //   })();
  // }, []);

  return (
    <Col span={20}>
      <Menu mode='horizontal'>
        <Menu.Item onClick={onRegister}>Register New User</Menu.Item>
        <Menu.Item onClick={onUsers}>Users</Menu.Item>
        <Menu.Item onClick={onPosts}>Posts</Menu.Item>
      </Menu>

      <div className={displayRegister ? classes.show : classes.hide}>
        <RegisterUserForm />
      </div>
      <div className={displayUsers ? classes.show : classes.hide}>
        <Table
          pagination={usersPagination}
          onChange={handleUsersChange}
          dataSource={users?.data?.allUsers?.nodes as User[]}
          columns={[
            {
              title: 'ID',
              dataIndex: 'userId',
              key: 'userId',
              ...getColumnSearchProps('userId'),
            },
            {
              title: 'Name',
              dataIndex: 'name',
              key: 'name',
              sorter: (a, b) =>
                a.name.toLowerCase() > b.name.toLowerCase() ? 1 : -1,
              ...getColumnSearchProps('name'),
            },
            {
              title: 'Username',
              dataIndex: 'username',
              key: 'username',
              sorter: (a, b) =>
                a.username.toLowerCase() > b.username.toLowerCase() ? 1 : -1,
              ...getColumnSearchProps('username'),
            },
            {
              title: 'Date Created',
              dataIndex: 'createdAt',
              key: 'createdAt',
              sorter: (a, b) =>
                a.createdAt.toLowerCase() > b.createdAt.toLowerCase() ? 1 : -1,
              // ...getColumnSearchProps('createdAt'),
              render: (a) => {
                let date = new Date(a);
                return <div>{date.toString()}</div>;
              },
            },
            {
              title: 'Last Updated',
              dataIndex: 'updatedAt',
              key: 'updatedAt',
              sorter: (a, b) =>
                a.updatedAt.toLowerCase() > b.updatedAt.toLowerCase() ? 1 : -1,
              // ...getColumnSearchProps('createdAt'),
              render: (a) => {
                let date = new Date(a);
                return <div>{date.toString()}</div>;
              },
            },
            {
              title: 'Actions',
              render: (a) => {
                return (
                  <div>
                    <button
                      className={
                        a.accountVerified ? classes.hide : classes.show
                      }
                      onClick={() =>
                        handleVerifyUser(a.userId, a.accountVerified)
                      }
                    >
                      Verify
                    </button>
                    <button
                      className={
                        a.accountVerified ? classes.show : classes.hide
                      }
                      onClick={() =>
                        handleVerifyUser(a.userId, a.accountVerified)
                      }
                    >
                      Unverify
                    </button>
                    {/* <button onClick={() => handleDeleteUser(a.userId)}>
                      Delete
                    </button> */}
                  </div>
                );
              },
            },
          ]}
        />
      </div>
      <div className={displayPosts ? classes.show : classes.hide}>
        <Table
          loading={posts.loading}
          pagination={postsPagination}
          onChange={handlePostsChange}
          dataSource={posts?.data?.allPosts?.nodes as Post[]}
          columns={[
            {
              title: 'ID',
              dataIndex: 'postId',
              key: 'postId',
              ...getColumnSearchProps('postId'),
            },
            {
              title: 'Description',
              dataIndex: 'description',
              key: 'description',
              sorter: (a, b) =>
                a.description.toLowerCase() > b.description.toLowerCase()
                  ? 1
                  : -1,
              ...getColumnSearchProps('description'),
            },
            {
              title: 'Content',
              dataIndex: 'content',
              key: 'content',
              ...getColumnSearchProps('content'),
              render: (content) => {
                let c: Content[] = [];
                content.forEach((content: string) => {
                  c.push(JSON.parse(content));
                });
                return (
                  <div
                    style={{
                      maxWidth: '500px',
                      maxHeight: '200px',
                      overflowWrap: 'break-word',
                      overflowY: 'auto',
                    }}
                  >
                    {c.map((content) => {
                      let images: string[] = [];
                      if (content.type === 'text') {
                        return <div>{content.content}</div>;
                      } else {
                        (content.content as Image[]).forEach((image) => {
                          images.push(image.url);
                        });
                        return <Carousel images={images} />;
                      }
                    })}
                  </div>
                );
              },
            },
            {
              title: 'Date Created',
              dataIndex: 'createdAt',
              key: 'createdAt',
              sorter: (a, b) =>
                a.createdAt.toLowerCase() > b.createdAt.toLowerCase() ? 1 : -1,
              render: (a) => {
                let date = new Date(a);
                return <div>{date.toString()}</div>;
              },
            },
            {
              title: 'Last Updated',
              dataIndex: 'updatedAt',
              key: 'updatedAt',
              sorter: (a, b) =>
                a.updatedAt.toLowerCase() > b.updatedAt.toLowerCase() ? 1 : -1,
              render: (a) => {
                let date = new Date(a);
                return <div>{date.toString()}</div>;
              },
            },
            {
              title: 'Actions',
              render: (a) => {
                return (
                  <div>
                    <button onClick={() => handleDeletePost(a.postId)}>
                      Delete
                    </button>
                  </div>
                );
              },
            },
          ]}
        ></Table>
      </div>

      {/* <Admin dataProvider={dataProvider}>
        <Resource
          name='Post'
          list={PostList}
          edit={PostEdit}
          create={PostCreate}
        /> */}
      {/* <Resource name="Post" list={PostList} create={PostCreate} edit={PostEdit} /> */}
      {/* </Admin> */}
    </Col>
  );
};

export default AdminPage;
